// ** UseJWT import to get config
import useJwt from '../../../@core/auth/jwt/useJwt'

const config = useJwt.jwtConfig

// ** Handle User Login
export const handleLogin = (data) => {
  return (dispatch) => {
    dispatch({
      type: 'LOGIN',
      data,
      config,
      [config.storageTokenKeyName]: data[config.storageTokenKeyName],
      [config.storageRefreshTokenKeyName]:
        data[config.storageRefreshTokenKeyName]
    })

    // ** Add to user, accessToken & refreshToken to localStorage
    localStorage.setItem('userData', JSON.stringify(data))
    localStorage.setItem(
      config.storageTokenKeyName,
      JSON.stringify(data.accessToken)
    )
    localStorage.setItem(
      config.storageRefreshTokenKeyName,
      JSON.stringify(data.refreshToken)
    )
  }
}

// ** Handle User Logout
export const handleLogout = () => {
  localStorage.clear()
  return (dispatch) => {
    dispatch({
      type: 'LOGOUT',
      
      [config.data.data.token_type]: null,
      [config.data.data.access_token]: null
    })
    localStorage.clear()
    // ** Remove user, accessToken & refreshToken from localStorage
    window.localStorage.removeItem('userData')
    window.localStorage.removeItem(config.storageTokenKeyName)
    window.localStorage.removeItem(config.storageRefreshTokenKeyName)
  }
}
